* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  box-sizing: border-box;
}
:root {
  --page--color: #f2f3f4;
  --forcopy: #b3b6b7;
  --para-text: "Lato", sans-serif;
  --heading: "Ubuntu", sans-serif;
}
.universal {
  display: flex;
  justify-content: center;
  align-items: center;
}
header {
  height: 7vh;
  padding: 30px 70px;
}
li {
  list-style: none;
  padding: 0px 10px;
  font-family: var(--heading);
}
.para {
  gap: 4%;
  padding: 0px 10px;
}
.lot-details {
  flex-direction: column;
  background-color: var(--page--color);
  border-radius: 10px;
}
.para-height {
  padding: 15px 50px;
}
.para-height p {
  font-family: var(--para-text);
}
main {
  margin: 20px 0px;
}
article {
  width: 80vw;
  background-color: white;
}
.input-change {
  border: none;
  outline: none;
  border-bottom: 5px solid var(--forcopy);
  padding: 5px;
}
.para h3 {
  font-family: var(--para-text);
  padding: 40px 0px;
}
.spin {
  margin: 30px 0px;
  background-color: var(--page--color);
}
.copy-box {
  justify-content: end;
  width: 100%;
}
.copy-box .copytoclipboard {
  margin: 20px 60px 0px 0px;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
  background-color: var(--forcopy);
  font-family: var(--heading);
  font-weight: 500;
}
.typewriter-container{
  padding: 15px;
    padding-top: 46px;
}
.dynamic-text{
  font-family: var(--heading);
  font-size: 17px;
}

@media screen and (max-width : 550px) {
  .para-height {
    padding: 15px 20px;
  }
.copy-box .copytoclipboard {
  margin: 20px 26px 0px 0px;  }
}
